<template>
    <div class="overview">
        <DirectDebitOverview v-if="accountName" :accountname="accountName" />
    </div>
</template>

<script>
    // @ is an alias to /src
    import DirectDebitOverview from "@/components/DirectDebitOverview.vue";

    import beeet from "@/beet";

    export default {
        name: "overview",
        components: {
            DirectDebitOverview
        },
        data() {
            return {
                beet: beeet,
                accountName: null
            }
        },
        mounted() {
            if (this.$route.params.accountname) {
                this.accountName = this.$route.params.accountname;
            } else if (this.beet.getCurrentConnection()) {
                this.accountName = this.beet.getCurrentConnection().getAccount().name;
            }
            if (!this.accountName) {
                this.$router.push({name: "login", params: {next: "overview"}});
            }
        }
    };
</script>
