<template>
    <div class="login">
        <p v-if="$route.params.next">
            You need to be logged in to use this.
        </p>
        <button class="button" @click="connectToBeet">
            <BeetLogo/>
            <h1 class="title">Login</h1>
            <div class="sub-title">
                with Beet
            </div>
        </button>
    </div>
</template>

<script>
    // @ is an alias to /src
    import BeetLogo from "../components/BeetLogo";
    import beeet from "@/beet";

    export default {
        name: "home",
        components: {
            BeetLogo
        },
        data() {
            return {
                beet: beeet
            }
        },
        mounted() {
        },
        methods: {
            goToBeet() {
                window.open('https://github.com/bitshares/beet','_blank');
            },
            async connectToBeet() {
                let beetApp = await this.beet.connect();
                this.$router.push({ path: "overview/" + beetApp.getAccount().name });
            }
        }
    };
</script>

